.price-btn {
    color: #fff;
    display: block;
    padding: .5em 0;
    font-size: 1em;
    background: #00FFFF;
    border: 1px solid #000;
    margin: 0px;
}
.FontTextWhite10px {
    color: #fff;
    border: none;
    font-family: Verdana, Geneva, sans-serif;
    font-size: 11px;
    font-weight: 700;
    vertical-align: middle;
    background-color: #888399;
}

.FontText {
    color: #000;
    font-family: Verdana, Geneva, sans-serif;
    font-size: 11px;
    line-height: 20px;
    font-weight: 700;
}
.cricket_bet, .cricket_bet td{
    padding: 0px 2px;
    border: 2px solid #e9e9e9;
}