.TeamName {
    background: #7b7c7f;
    padding: .3em 0;
    border-top: 1px solid #7b7c7f;
    text-align: center;
    border-radius: 7px 7px 0 0;
    font-weight: 700;
    font-size: 1.1em;
}
.TeamName a {
    font-size: .85em;
    color: #fff;
    text-transform: uppercase;
    display: inline-block;
}
.profile-details {
    width: 100%;
    margin-right: 3%;
    background: #fff;
}
.profile-details a{
   color: #000;
}

.GameList {
    color: #000;
    font-family: Verdana, Geneva, sans-serif;
    font-size: .8em;
    line-height: 22px;
    font-weight: 700;
}
.no_data_cound{
    font-size: 16px;
    padding: 12px;
    background-color: #fff;
}